.navbar-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin: 16px 16px 16px 32px;
  background: transparent;
}

.navbar {
  font-family: "Noto Sans", sans-serif;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: transparent;
}

.navbar-title {
  font-size: 44px;
  font-weight: 800;
}

.navbar-logo {
  background-image: url("../images/transparent-V2.png");
  display: block;
  height: 45px;
  width: 300px;
  background-size: contain;
  background-repeat: no-repeat;
  flex-grow: 1;
}
